import { AuthProvider } from './../../providers/auth/auth';
import { ValidateProvider } from './../../providers/validate/validate';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, Events } from 'ionic-angular';
import { FormGroup } from '@angular/forms';

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  // formulario de login
  public loginForm: FormGroup;

  // validador do formulario
  public validate: ValidateProvider;

  // inicia os erros
  public erro = "";

  // Pagina de redirecionamento apos o login
  public redirectPage;

  constructor(public navCtrl: NavController, 
              public navParams: NavParams,
              public loadingCtrl: LoadingController,
              public auth: AuthProvider,
              public events: Events) {
    
    // Pega a pagina de redirecionamento
    this.redirectPage = this.navParams.get( 'redirectPage' ) ? true : false;

    // inicia o formulario
    this.initForm();
  }

  /**
   * initForm
   * 
   * Inicia o formulario de login
   */
  public initForm() {

    // cria o loading
    const loading = this.loadingCtrl.create( {} );
    loading.present();

    // instancia um novo validador
    this.validate = new ValidateProvider();

    // seta as regras de validação
    const rules = {
      email    : 'required|valid_email',
      password : 'required|maxLength[18]|minLength[6]'
    };

    // seta o formulario
    this.loginForm = this.validate.set_form( rules );

    // tira o loading
    loading.dismiss();
  }

  /**
   * doLogin
   * 
   * faz o login no aplicativo
   */
  public login() {

    // cria o loading
    const loading = this.loadingCtrl.create({});
    loading.present();

    // pega os valores do formulario
    const dados = JSON.parse( JSON.stringify( this.loginForm.value ) );

    // tenta fazer o login
    this.auth.login( dados )
    .then( user => {

      // redireciona para as tabs
      if( this.redirectPage ) {
        this.navCtrl.pop();
      } else this.navCtrl.setRoot( 'FeedPage' );

      // Dispara o evento
      this.events.publish( 'atualizar:true' );
      this.events.publish( 'login' );
      
    })
    .catch( err => {

      // seta os erros
      this.erro = err;
    })
    .then( () => loading.dismiss() );
  }
  
  /**
   * goToRegisterPage
   * 
   * redireciona para a pagina de cadastro
   */
  public goToRegisterPage() {
    this.navCtrl.push( "CadastroPage" );
  }

  /**
   * goToForgotPassword
   * 
   * redireciona para a pagina de esqueci minha senha
   */
  public goToForgotPasswordPage() {
    this.navCtrl.push( "EsqueciSenhaPage" );
  }

  public back() {
    if( this.navCtrl.canGoBack() ) {
      this.navCtrl.pop();
    } else this.navCtrl.push( 'FeedPage' );
  }
}

